import React from 'react';
import Latex from 'react-latex';
export default function NumeroCondicao(){ return(<>
<h2>
    Número de Condição
</h2>

É uma medida dada por <Latex>{"$\\mathcal{K}(A) = ||A||\\;||A^{-1}||$"}</Latex>

<br />

<b> Prova: </b> Seja <Latex>{"$Ax=b$"}</Latex> um sistema linear, e <Latex>{"$A(x +\\Delta x)=b+\\Delta b$"}</Latex> um sistema linear análogo ao primeiro, mas com uma perturbação no vetor b (<Latex>{"$\\Delta b$"}</Latex>) oq ue implica que a solução também terá um perturbação(<Latex>{"$\\Delta x$"}</Latex>), então temos que

<Latex displayMode={true}>{'$$'+
'A(x +\\Delta x)=b+\\Delta b \\Leftrightarrow Ax + A\\Delta x = b+\\Delta b \\stackrel{Ax=b}{\\Leftrightarrow} A\\Delta x = \\Delta b \\Leftrightarrow \\Delta x = A^{-1} \\Delta b'
+'$$'}</Latex>

Usando propriedades de norma, temos que <Latex>{"$||b||=||Ax||=\\leq||A||\\;||x||$"}</Latex>, então <Latex>{"$\\frac{1}{||b||}\\geq\\frac{1}{||A||\\;||x||}$"}</Latex>

<br />

Usando esses dois resultados, chegamos no número de condição

<Latex displayMode={true}>{'$$'+
'\\frac{1}{||b||}\\geq\\frac{1}{||A||\\;||x||} \\Leftrightarrow'+
'\\frac{||\\Delta x||}{||b||}\\geq\\frac{||\\Delta x||}{||A||\\;||x||} \\Leftrightarrow '+
'\\frac{||A^{-1} \\Delta b||}{||b||}\\geq\\frac{||\\Delta x||}{||A||\\;||x||}\\Leftrightarrow '+
'\\frac{||\\Delta b||}{||b||} ||A^{-1} ||\\; ||A||\\geq\\frac{||\\Delta x||}{||x||}'
+'$$'}</Latex>



<h3>
Propriedades
</h3>

<ul>
    <li>
        <Latex>{"$\\mathcal{K}(A)=||A||\\;||A^{-1}||=||A^{-1}||\\;||A||=||A^{-1}||\;||(A^{-1})^{-1}||=\\mathcal{K}(A^{-1})$"}</Latex>
    </li>
    <li>
        <Latex>{"$\\mathcal{K}(cA)=||cA||\\;||(cA)^{-1}||=||cA||\\;||c^{-1}A^{-1}||=\\mid c\\mid||A||\\mid c^{-1}\\mid||A^{-1}||=\\frac{\\mid c\\mid}{\\mid c\\mid}||A||\\;||A^{-1}||=||A||\\;||A^{-1}||=\\mathcal{K}(A)$"}</Latex>
    </li>
    <li>
        <Latex>{"$\\mathcal{K}(A)= ||A||\\;||A^{-1}|| \\geq ||AA^{1}|| = ||\\mathbf{I}|| = 1$"}</Latex>
    </li>
</ul>

</>)}